/* General */
.CardBody {
	padding-top: 5px;
	padding-bottom: 5px;
	box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.3);
}

.CommentArea {
	padding-top: 5px;
}

.ReplyCommentArea {
	padding-top: 25px;
}

/* Profile */
.ProfileSection {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
}

.ProfileName {
	font-size: 12px;
	margin: 5px;
}

.ProfileImage {
	width: 50px;
	height: 50px;
	border-radius: 50%;
	object-fit: cover;
	margin: 5px;
}

/* Form Section */
.FormControl {
	font-size: 0.8rem;
	color: blue;
}

.SubmitButton {
	padding-bottom: 10px;
}

.ButtonSpacing {
	margin-right: 10px;
	margin-top: 10px;
	font-size: x-small;
}

/* Display Success Message */
.SuccessMessage {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 1000;
}
